export class RequestFacetQueriesInner {
    /**
     * A facet query
     */
    query?: string;
    /**
     * A label to include in place of the facet query
     */
    label?: string;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
        }
    }

}
